.main-nav{
    display:flex;
   justify-content:space-between;
}



.menu-Link ul{
    display: flex;
    margin-top: revert;
   
}
.social-media ul{
display:flex ;
margin-top: revert;

}
ul li{
    margin-right: 50px;
   
    list-style-type: none;
    font-weight: bold;
    

}
ul li a{ text-decoration:none!important ;
    color: red;}
.logo img{
height: 50px;
    width: 66px;
}
.facebook{
    color:	#0165E1 ;
}
.instagram{
    background:linear-gradient(purple,rgb(226, 94, 154),red,orange,yellow);
    border-radius: 25%;
    color:aliceblue;
    }
.youtube{
    color: red;
}




.hamburger{
    display: none;
}

@media(max-width:711px){
    ul li {
        margin-right: 23px;
    }
}

@media(max-width:530px){


    /* .main-nav{
        height: 49px;
    } */
    .social-media{
        display: none;
    }

    .menu-Link{
        display: none;
    }

    .hamburger{
        display: block;
    }

    .mobile-menu{
        display: grid;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
        height: 86px;
        width: 430px;
        margin-top: 74px;
    margin-left: -39px; 
    margin-bottom: 20px;
    }

        
  

    .menu-Link ul li{
          margin: 8px 16px;
    }
}
