.foot{
    background-color: rgb(39, 40, 112);
    height: 300px;
    color: white;
    font-family: 'Noto Sans', sans-serif;
    padding: 50px;
    display: flex;
    justify-content: space-around;
}
.foot p{
    font-size: 15px;
    margin: 12px 0px;
    /* text-align: start; */
}
.content{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* margin-left: 440px; */
}
.icon1{
    margin-right: 10px;
}
.par{
    display: flex;
}

.coninf{
    display: flex;
    margin: 15px 0px;
}
.icon2{
    margin-right: 10px;
}

.mailinf{
    display: flex;
    margin: 15px 0px;
}
.icon3{
    margin-right: 10px;
}

.content2{
    margin-top: 70px;
}
.icon4,.icon5,.icon6{
    margin-right: 28px;
    cursor: pointer;
    /* text-align: center; */
}
.sicons{
    display: flex;
}

@media (max-width:433px){
    .hfour{
        font-size: 15px;
    }

    .foot p{
        font-size: 9px;
    }
    
    .number{
        font-size: 9px;
    }

    .mailcont{
        font-size: 9px;
    }

    .content2 {
        margin-top: 0px;
    }

    .icon4, .icon5, .icon6 {
        margin-right: 17px;
    }

    .content2 h4{
        font-size: 15px;
    }
}